export default{
	data(){
		return{
			statArr:["待付款","","失效","未发货","已发货","","","申请退款","退款成功","","","","待评价","交易完成","交易关闭"],
			orderInfo:"",
			orderId:"",
			shopObj:{}
		}
	},
	onLoad(e){
			let that=this;
			that.orderId=e.id;
			uni.getStorage({
				key:"shopObj",
				success(res){
					that.shopObj=JSON.parse(res.data);
					console.log(that.shopObj)
				}
			})
	},
	onShow(){
		let that=this;
		that.$ajax({
			url:"/order/detail",
			data:{
				orderId:that.orderId
			},success(d){
				d.address=JSON.parse(d.address)
				that.orderInfo=d;
				console.log(that.orderInfo)
			}
		})
	}
}
